.theam-control{
position:fixed;
/* top: 10px; */
left: 10px;
width: 80px;
height: 30px;
cursor: pointer;
/* border: 1px solid var(--secondary-color); */
/* color: var(--secondary-color); */
}
.theam-control>img{
    width: 70px;
}
.logo{
    width: 50px;
    height: 50px;
    top: 10px;
    position: relative;

}
.n-bar{
height: 12vh;
/* border: 1px solid black; */
/* background-color: black; */
display: flex;
width: 90%;
margin: auto;
flex-direction: row;
justify-content: space-between;
border-bottom: 1px solid var(--title-color);
border-width: 80%;
}
.n-left{
    width:30%;
    color: white;
    /* border: 1px solid rgb(235, 235, 235); */
    justify-content: space-around;
    display: flex;
    /* padding: 0px 80px 0px 20px; */
  

}

.n-ul-list{
    display: flex;
    color:var( --secondary-color);
    justify-content: space-evenly;
}
.n-ul-list>li>*{
    list-style: none;
    cursor: pointer;
    font-size: 15px;
}
.n-ul-list>li{
    list-style: none;

}
.n-right{
    width: 60%;
    /* border: 1px solid rgb(235, 235, 235); */
 
}


.contact-button{
    color: var(--secondary-color);
    top: -8px;
    position: relative;
    background-color:var(--title-color);
    border-radius: 20px;
    font-size: 15px;
    padding: 10px;
}
.n-ul-list>li:hover{
    color: var(--title-color);
}
.namedipak{
    color: var( --secondar-color);
}

@media screen and (max-width: 480px) {
    .n-right {
      display: none;
    }
    .n-bar{
    
    }
    .n-left{
width: 100vw;
justify-content: space-between;
padding: 0px 10px 0px 10px;
    }

