.home-container {
  display: flex;
  justify-content: space-between;
  position: relative;
  width: 90vw;
 
  margin: auto;

}

#home{
  position: relative;
}
.l-title {
  left: -100px;
  position: relative;
  width: 80px;
}
.video{
  width: 100%;

  position: absolute;
}
.h-leftdiv {
  width:80vw;

text-align: start;

  color: var(--title-color);
}
.l-title {
    font-size: 30px;
    text-align: start;
    
  }
  .web-developer{
    position: absolute;
    left: 100px;
  }
.h-left-name {

  width: 80%;
  top: 90px;
  left: 50px;
  position: relative;

}

.my-name {
  color: var(--title-color);
  font-size: 85px;
  position: relative;
  left: 20px;
}

.cv-button{
    color: var(--primary-color);
    top: 200px;
    position: relative;
    background-color:var(--title-color);
    border-radius: 20px;
    padding: 10px;
    left: 200px;
}
.video{
  position: absolute;
}


.h-rightdiv {
  width: 60vw;
top: 100px;
  position: relative;

}
