@media screen and (max-width: 480px) {

    .home-container {
        display: flex;
        flex-direction: column;
            position: relative;
            width: 100vw;
            margin: auto;
     
      }

  
      .h-left-name {
        top: 20px;
        left: 20px;
      }
      .web-developer{
  
        left: 100px;
        font-size: 14px;
      }
      .hero-img {
        width: 70vw;
        height: 48vh;
        left: -20px;
        top:180px;
      }
      .social-icons{

        top: 150px;
        left: 180px;
        width: 60%;
        }
        .social-icons>a>img{
            width: 30px;
        }
        .cv-button{
            top: 20px;
            padding: 5px;
            left: 300px;
            font-size: 10px;
        }

        .h-rightdiv {
          width: 60vw;
        top: 10px;
          position: relative;
        
        }
        .web-developer{
          position: relative;
          left: 10px;
          font-size: 10px;
          top: 20px;
        }
        .my-name {
          color: var(--title-color);
          font-size: 35px;
          position: absolute;
          /* left: 20px; */
        }
}