.skill-titl {
    font-size: 50px;
    color: var(--img-hero);
 
  }
  .tools-use{
    display: flex;
    width: 70vw;
    /* border: 1px solid red; */
    align-items: center;
    height: 40vh;
    justify-content: space-evenly;
    margin: auto;
  }
  .tools-use>img{
width: 100px;
/* border: 1px solid red; */
height: 18vh;
padding: 20px;
background-color: black;
border-radius: 20px;
  }

  .svgnet{
    width: 100px;
    color: white;
  }

  @media screen and (max-width: 480px) 
{
    .skill-titl {
        font-size: 35px;
      }
      .tools-use{
        display: grid;
        grid-template-columns: repeat(4,1fr);
        width: 50vw;
       /* flex-wrap: wrap; */
        align-items: center;
        height: 10vh;
        justify-content: space-evenly;
        margin: auto;
      }
      .tools-use>img{
        width: 50px;
        /* border: 1px solid red; */
        height: 10vh;
        padding: 10px;
        background-color: black;
        border-radius: 20px;
          }
}