.c-title{
    font-size: 55px;
    color: var(--img-hero);
    margin-top: 200px;
}

.C-div{
    width: 95vw;
    height: 90vh;
    display: flex;
}

.c-left-div{
    width: 50%;
}

.c-left-div>img{
    width: 500px;
}
.c-right-div{
    width: 40%;
    border: 1px solid rgb(255, 255, 255);
    border-radius: 50px;
    background-color: var(--y-w);
    color: var( --secondary-color);
    padding: 40px;
    height: 75vh;
}
.c-right-div>form{
    display: flex;
    flex-direction: column;
    /* align-items: center; */
    justify-content: center;
    margin: auto;
    background-color: var(--y-w);
     /* border: 1px solid rgb(86, 2, 2); */
    width: 70%;
}
.c-right-div>form>*{
    margin-top: 40px;
    text-align: start;
    padding: 10px;
    border: none;
    border-bottom: 1px solid var( --secondary-color);
    background-color: var(--y-w);
    color: var( --secondary-color);
}

.c-right-div>h2{
    font-size: 30px;
    width: 70%;
    margin: auto;
    text-align: start;
    background-color: var(--y-w);
}
.s-btn{
    width: 120px;
 display: flex;
    justify-content: center;
    border-radius: 15px;
  background-color: rgb(163, 161, 161);
  border: none;
  color: var( --secondary-color);

}

@media screen and (max-width: 480px) {

    .c-title{
        font-size: 35px;
        color: var(--title-color);
        margin-top: 150px;
    }
    .C-div{
        width: 95vw;
    }
    
    .c-left-div{
        width: 45%;
    }
    
    .c-left-div>img{
        width: 150px;
    }
    .c-right-div{
        width: 40%;
        border: 1px solid rgb(255, 255, 255);
        border-radius: 50px;
        background-color: var(--y-w);
        color: white;
        padding: 30px;
        height: 35vh;
    }
    .c-right-div>form{
        display: flex;
        flex-direction: column;
        /* align-items: center; */
        justify-content: center;
        margin: auto;
        background-color: var(--y-w);
         /* border: 1px solid rgb(86, 2, 2); */
        width: 90%;
    }
    .c-right-div>form>*{
        margin-top: 10px;
        padding: 0px;
        font-size: 14px;
    }
    .c-right-div>h2{
        font-size: 12px;
        width: 90%;
    }
    .s-btn{
        width: 100px;
 

    
    }
}