
*{
  background-color: var(--primary-color);
  text-align: center;
}
.lightheam{
  --primary-color:#ffffff;
  --secondary-color:rgb(0, 0, 0);
  --title-color : #FCA61F;
  --themblack-color:black;
  --img-hero:black;
  --img-back: #FCA61F;
  --y-b:#FCA61F;
  /* //contrast yello */
  --y-w:#FCA61F;
}
.darktheam{
  --primary-color:#18182a;
  --secondary-color:rgb(255, 255, 255);
  --title-color : #FCA61F;
  --themblack-color:black;
  --img-hero: #FCA61F;
  --img-back: #000000;
  /* //w-y */
  --y-w:#ffffff;
  --y-b:#000000;
}

.div{
  position: absolute;
}
.video{
  position: relative;
}
.name{
  color: var( --secondart-color);
}


@media screen and (max-width: 480px) {
  .App {
    padding: 0.5rem 1rem;

  }
}




