#About{
    color: white;
    display: flex;
    justify-content: space-evenly;
    position: relative;
    margin-top: 200px;
    width: 95vw;
}

.about-title{
    font-size: 60px;
    color: var(--img-hero);
}
.about-des{
    width:50vw ;
    text-align: start;
    font-size: 22px;
line-height: 35px;
color: var(--img-hero);
}

.vide{
    width: 100%;
    position: relative;
}
.social-icons{

    top: 50px;
    position: relative;
    display: flex;
    width: 30%;
    margin: auto;
    justify-content: space-evenly;
    }
    .social-icons>a>img{
        width: 30px;
    }




.about-pic{
    width: 20vw;
    height: 20vw;
    border-radius: 200px;
    box-shadow:var(--img-hero) 0px 25px 50px -12px;
}
.about-pic:hover{
    transform: scale(1.2);
}

@media screen and (max-width: 480px) {

    #About{
    width: 95vw;
    position: relative;
    justify-content: space-between;
    /* top: 200px; */
    margin-top: 70px;
    }
    .about-title{
        font-size: 35px;
    
    }
    .about-des{
        font-size: 10px;
    line-height: 20px;
    }

    .social-icons{

        top: 100px;
        
        display: none;
        width: 90%;
        /* margin: auto; */
        /* justify-content: space-evenly; */
        }
        .social-icons>a>img{
            width: 10px;
        }
    
.about-pic{
    width: 40vw;
    height: 40vw;
    position: relative;
    top: 50px;
    border-radius: 200px;
    box-shadow:var(--title-color) 0px 25px 50px -12px;
}
}