.p-outerdiv{

    margin: auto;
    /* border: 1px solid green; */
    width: 90%;
    position: relative;
    
   
   
}

.p1-data{
    /* border: 1px solid red; */
    box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
    width: 80%;
    height: 300px;
    margin: auto;
    display: flex;
    padding: 20px;
    flex-direction:row;
    gap: 40px;
    border-radius: 20px;
    background-color:var(  --img-back);
    
}
.pdiv{
    height: auto;
    /* margin-top: 100px; */
    /* border: 1px solid rgb(251, 251, 251); */
    padding: 20px;
   /* border: 1px solid rgb(211, 210, 210); */
    border-width: 70%;
    /* background-color:var( --themblack-color); */
}

.pdiv>h1{
    font-size: 30px;
   /* text-decoration: underline; */
   margin-bottom: 50px;
    display: block;
}
.pdiv>h1>span{
    font-size: 18px;
   
}
.p1-data>div{
width: 70%;
}
/* .p-cover>img{
   

} */

#projects{
    margin-top: 150px;
    color: var( --secondary-color);
}

#projects>h1{
    font-size: 50px;
    color: var(--img-hero);
}
.p-cover{
    border-radius: 20px;
}
.cover-img{
width: 100%;
border-radius: 20px;

height: 100%;
}


.btn-div>a>button{
    color: rgb(0, 0, 0);
    background-color: white;
    cursor: pointer;
    border-radius: 20px;
    padding: 5px;
}

.btn-div>a{
    background-color: var(--img-back);
}
.btn-div{
    /* margin-top: 20px; */
    display: flex;
    width: 40%;
    justify-content: space-between;
    /* flex-direction: column; */
}

.h{
    color: white;
}
.t-staack{
    display: flex;
    /* gap: 20px; */
    justify-content: space-between;
   
    /* border: 1px solid red; */
    width: 50%;
    background-color:var(--img-back);
    margin: auto;
    margin-top: 30px;
}
.t-staack>img{
    width: 3vw;
    height: 6vh;
    background-color:var( --img-back);
}

.p-info{
    background-color:var(--img-back);
}
.p-info>h1{
    font-size: 25px;
}
.p-info>h3{
    font-size: 15px;
}
.p-info>*{
    background-color:var( --img-back);
    text-align: start;
}
.dec-pro{
    font-size: 14px;
}

@media screen and (max-width: 480px) 
{
    .p-outerdiv{

        margin: auto;
        /* border: 1px solid green; */
        width: 98%;
        position: relative;
        
       
       
    }  
    
.p1-data{
    /* border: 1px solid red; */
    box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
    width: 100%;
    height: 200px;
    margin: auto;
    display: flex;
    padding: 10px;
    flex-direction:row;
    gap: 10px;
    border-radius: 10px;
    background-color:var( --themblack-color);
    
}

.pdiv>h1{
    font-size: 20px;

   margin-bottom: 20px;
   
}
.p1-data>div{
    width: 90%;
    }
    #projects{
        margin-top: 100px;
        color: white;
    }
    #projects>h1{
        font-size: 35px;
       
    }
    .cover-img{
        width: 100%;
        border-radius: 20px;
        height: 100%;
        }
        .btn-div>a>button{
            color: rgb(0, 0, 0);
            background-color: white;
            cursor: pointer;
            border-radius: 20px;
            padding: 2px;
            font-size: 10px;
            width: 70px;
        }
        .t-staack{
            width: 40%;
            background-color:var(  --img-back);
            margin: auto;
            margin-top: 10px;
        }
        .t-staack>img{
            width: 3vw;
            height: 2vh;
            background-color:var(  --img-back);
        }
        .p-info>h1{
            font-size: 15px;
        }
        .p-info>h3{
            font-size: 15px;
        }
        .p-info>*{
            background-color:var(  --img-back);
            text-align: start;
        }
        .dec-pro{
font-size: 8px;      }
}