.type-div{
    color: var( --secondary-color);
    position: relative;
    font-size: 1px;
    left: 150px;
    font-size: 12px;
}



@media screen and (max-width: 480px) {
    .type-div{
      
        left: 10px;
        font-size: 10px;
    }
    .type-div>*{
      
        left: 10px;
        font-size: 8px;
    }
}

