.e-title{
    font-size: 50px;
    color: var( --img-hero);
}
#education{
    margin-top: 200px;
}
.hr{
rotate: 90deg;
height: 0.5px;
width: 620px;
position: relative;
top: 350px;
left: 50px;
}
.e-first-div{
    /* border: 1px solid white; */
    display: block;
    color:var(--secondary-color);
    width:30vw;
    position: relative;
    left: 100px;
    background-color:var( --img-back);
    padding: 30px;
    border-radius: 40px;
gap: 200px;
    /* box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset; */
    
    
}
.e-first-div>h1{
    font-size: 25px;
}
.e-first-div>h2{
    font-size: 15px;
}
.e-first-div>h5{
    font-size: 12px;
}
.e-first-div>*{
    background-color:var( --img-back);
    text-align: start;
}
#second{
    left: 820px;
}


@media screen and (max-width: 480px) 
{
    .e-title{
        font-size: 30px;
    }
    #education{
        margin-top: 100px;
    }
    .hr{
        rotate: 90deg;
        height: 0.5px;
        width: 280px;
        position: relative;
        top: 180px;
        left: 0px;
        }
        #second{
            left: 210px;
        }
        .e-first-div{
            /* border: 1px solid white; */
          
            width:35vw;
            position: relative;
            left: 0px;
            background-color: rgb(0, 0, 0);
            padding: 10px;
            color: var( --primary-color);
            border-radius: 40px;
        /* gap: 200px; */
            /* box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset; */
            
            
        }
        .e-first-div>h1{
            font-size: 12px;
        }
        .e-first-div>h2{
            font-size: 10px;
        }
        .e-first-div>h5{
            font-size: 8px;
        }
        .e-first-div>*{
            background-color: rgb(0, 0, 0);
            text-align: center;
        }
}